
.cartcontrol {
  font-size: 0;
}
.cartcontrol .move-enter-active,
.cartcontrol .move-leave-active {
  transition: all 0.5s;
}
.cartcontrol .move-leave-active {
  transition: all 0.5s;
  -webkit-transform: translate(30px) rotate(90deg);
          transform: translate(30px) rotate(90deg);
}
.cartcontrol .move-enter,
.cartcontrol .move-leave-active {
  -webkit-transform: translate(20px) rotate(90deg);
          transform: translate(20px) rotate(90deg);
  opacity: 0;
}
.cartcontrol .cart-reduce {
  display: inline-block;
  padding: 0.12rem;
  width: 0.36rem;
}
.cartcontrol .cart-reduce i {
  font-size: 0.36rem;
  line-height: 0.36rem;
  color: #00a0dc;
}
.cartcontrol .cart-count {
  display: inline-block;
  font-size: 0.2rem;
  padding: 0.12rem;
  vertical-align: top;
  text-align: center;
  color: gray;
  width: 0.24rem;
  line-height: 0.36rem;
}
.cartcontrol .cart-add {
  display: inline-block;
  padding: 0.12rem;
}
.cartcontrol .cart-add i {
  font-size: 0.36rem;
  line-height: 0.36rem;
  color: #00a0dc;
}



