
.star .star-text {
  font-size: 0.4rem;
}
.star .star-item {
  display: inline-block;
  width: 0.3rem;
  height: 0.3rem;
  margin-right: 0.1rem;
}
.star .star-item.on {
  background-image: url('star_on.png');
  background-size: 0.3rem 0.3rem;
  background-repeat: no-repeat;
}
.star .star-item.half {
  background-image: url('star_half.png');
  background-size: 0.3rem 0.3rem;
  background-repeat: no-repeat;
}
.star .star-item.off {
  background-image: url('star_off.png');
  background-size: 0.3rem 0.3rem;
  background-repeat: no-repeat;
}



