
.shopcart {
  height: 0.96rem;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 50;
}
.shopcart .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #141d27;
}
.shopcart .content .content-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.shopcart .content .content-left .logo-wrap {
  display: inline-block;
  vertical-align: top;
  position: relative;
  top: -0.2rem;
  margin-left: 0.24rem;
  margin-bottom: 0.04rem;
  padding: 0.12rem;
  background: #141d27;
  border-radius: 50%;
  box-sizing: border-box;
}
.shopcart .content .content-left .logo-wrap .logo {
  width: 0.88rem;
  height: 0.88rem;
  line-height: 0.88rem;
  text-align: center;
  background-color: #2b343c;
  border-radius: 50%;
}
.shopcart .content .content-left .logo-wrap .logo.highlight {
  background: #00a0dc;
}
.shopcart .content .content-left .logo-wrap .logo i {
  font-size: 0.46rem;
  color: rgba(255, 255, 255, 0.4);
}
.shopcart .content .content-left .logo-wrap .logo i.highlight {
  color: white;
}
.shopcart .content .content-left .logo-wrap .num {
  position: absolute;
  top: 0;
  right: 0;
  width: 0.48rem;
  height: 0.32rem;
  line-height: 0.37rem;
  text-align: center;
  border-radius: 0.16rem;
  font-size: 0.09rem;
  font-weight: 700;
  color: white;
  background: #f01414;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}
.shopcart .content .content-left .price {
  display: inline-block;
  font-size: 0.48rem;
  font-weight: 700;
  line-height: 0.48rem;
  color: rgba(255, 255, 255, 0.4);
  vertical-align: top;
  margin-top: 0.24rem;
  box-sizing: border-box;
  padding-right: 0.24rem;
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}
.shopcart .content .content-left .price.highlight {
  color: #fff;
}
.shopcart .content .content-left .desc {
  display: inline-block;
  font-size: 0.30rem;
  font-weight: 300;
  line-height: 0.48rem;
  color: rgba(255, 255, 255, 0.4);
  vertical-align: top;
  margin-top: 0.28rem;
  box-sizing: border-box;
  padding-left: 0.24rem;
}
.shopcart .content .content-righ {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 2.05rem;
          flex: 0 0 2.05rem;
  width: 2.05rem;
}
.shopcart .content .content-righ .pay {
  font-size: 0.24rem;
  font-weight: 700;
  line-height: 0.98rem;
  height: 0.96rem;
  color: #26333b;
  background-color: #2b343c;
  color: rgba(255, 255, 255, 0.4);
  text-align: center;
}
.shopcart .content .content-righ .pay.not-enough {
  background: #2b333b;
}
.shopcart .content .content-righ .pay.enough {
  background: #00b43c;
  color: #fff;
}
.shopcart .movetop-enter-active,
.shopcart .movetop-leave-active {
  transition: all 0.8s;
  opacity: 1;
}
.shopcart .movetop-enter,
.shopcart .movetop-leave-active {
  -webkit-transform: translate(0, 6rem);
          transform: translate(0, 6rem);
  opacity: 0;
}
.shopcart .shopcart-list {
  top: -5rem;
  position: absolute;
  left: 0;
  height: 6rem;
  z-index: -1;
  width: 100%;
  background: #fff;
}
.shopcart .shopcart-list .list-header {
  height: 0.8rem;
  background: #f3f5f7;
  line-height: 0.8rem;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  padding: 0 0.36rem;
  font-weight: 200;
  overflow: hidden;
}
.shopcart .shopcart-list .list-header .title {
  float: left;
  font-size: 0.28rem;
}
.shopcart .shopcart-list .list-header .empty {
  float: right;
  font-size: 0.24rem;
  color: #00a0dc;
}
.shopcart .shopcart-list .list-content {
  padding: 0 0.36rem;
  max-height: 4.34rem;
  overflow: hidden;
}
.shopcart .shopcart-list .list-content .food {
  position: relative;
  padding: 0.24rem 0;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.shopcart .shopcart-list .list-content .food .name {
  line-height: 0.48rem;
  font-size: 0.28rem;
  color: #07111b;
}
.shopcart .shopcart-list .list-content .food .price {
  position: absolute;
  right: 1.8rem;
  bottom: 0.24rem;
  line-height: 0.3rem;
  font-size: 0.28rem;
  font-weight: 700;
  color: #f01414;
}
.shopcart .shopcart-list .list-content .food .cartcoltrol-wrap {
  position: absolute;
  right: 0;
  bottom: 0.12rem;
}
.mask-enter-active,
.mask-leave-active {
  transition: all 0.8s;
  opacity: 1;
}
.mask-enter,
.mask-leave-active {
  opacity: 0;
}
.list-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -2;
  background: rgba(7, 17, 27, 0.6);
}



