
#app .tab {
  height: 0.8rem;
  line-height: 0.8rem;
  width: 100%;
  font-size: 0.28rem;
  border-bottom: 0.01rem solid #d0d8e0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: #07111b;
}
#app .tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
#app .tab .tab-item > a {
  display: block;
}
#app .tab .tab-item > a.router-link-active {
  color: #f01414;
}



