
.header {
  background: rgba(7, 17, 27, 0.5);
  width: 100%;
  height: 2.7rem;
  position: relative;
  overflow: hidden;
  color: white;
}
.header .fade-enter-active,
.header .fade-leave-active {
  transition: opacity .8s;
}
.header .fade-enter,
.header .fade-leave-active {
  opacity: 0;
}
.header .detail {
  -webkit-backdrop-filter: blur;
          backdrop-filter: blur;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(7, 17, 27, 0.8);
  z-index: 9999;
  overflow: auto;
}
.header .detail .detail-wrap {
  min-height: 100%;
  width: 100%;
}
.header .detail .detail-wrap .detail-main {
  margin-top: 1.28rem;
  padding-bottom: 1.28rem;
}
.header .detail .detail-wrap .detail-main .name {
  line-height: 0.32rem;
  text-align: center;
  font-size: 0.32rem;
  font-weight: 700;
}
.header .detail .detail-wrap .detail-main .star-wrap {
  margin-top: 0.32rem;
  padding: 0.02rem 0;
  text-align: center;
}
.header .detail .detail-wrap .detail-main .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  margin: 0.56rem auto 0.48rem auto;
}
.header .detail .detail-wrap .detail-main .title .line {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 2.24rem;
  height: 1px;
  background: rgba(255, 255, 255, 0.2);
}
.header .detail .detail-wrap .detail-main .title .text {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 0.3rem;
  text-align: center;
  position: relative;
  top: -0.16rem;
  font-weight: 700;
}
.header .detail .detail-wrap .detail-main .adv {
  padding: 0.48rem 0.72rem;
  font-size: 0.24rem;
  font-weight: 200;
  line-height: 0.48rem;
}
.header .detail .detail-wrap .detail-main .supports {
  margin: 0.48rem auto 0.56rem 0.96rem;
}
.header .detail .detail-wrap .detail-main .supports .support-item {
  height: 0.32rem;
  font-size: 0.24rem;
  font-weight: 200;
  line-height: 0.24rem;
  padding: 0 0.12rem;
  margin-bottom: 0.24rem;
}
.header .detail .detail-wrap .detail-main .supports .support-item .icon {
  width: 0.36rem;
  height: 0.36rem;
  margin-right: 0.14rem;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 0.36rem 0.36rem;
  vertical-align: middle;
}
.header .detail .detail-wrap .detail-main .supports .support-item .icon.decrease {
  background-image: url('decrease.png');
}
.header .detail .detail-wrap .detail-main .supports .support-item .icon.discount {
  background-image: url('discount.png');
}
.header .detail .detail-wrap .detail-main .supports .support-item .icon.guarantee {
  background-image: url('guarantee.png');
}
.header .detail .detail-wrap .detail-main .supports .support-item .icon.special {
  background-image: url('special.png');
}
.header .detail .detail-wrap .detail-main .supports .support-item .icon.invoice {
  background-image: url('invoice.png');
}
.header .detail .detail-close {
  position: relative;
  width: 0.64rem;
  height: 0.64rem;
  margin: -0.64rem auto;
  clear: both;
  font-size: 0.32rem;
  color: white;
}
.header .background {
  -webkit-filter: blur(5px);
          filter: blur(5px);
}
.header .background img {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
}
.header .heder-top {
  height: 2.1rem;
  box-sizing: border-box;
  position: relative;
  padding: 0.48rem 0.24rem 0.36rem 0.48rem;
}
.header .heder-top .head {
  display: inline-block;
}
.header .heder-top .head img {
  width: 1.28rem;
  height: 1.28rem;
  border-radius: 0.04rem;
}
.header .heder-top .content {
  display: inline-block;
  margin-left: 0.32rem;
  color: white;
}
.header .heder-top .content .title .brand {
  width: 0.6rem;
  height: 0.32rem;
  display: inline-block;
  background-image: url('../../imgs/brand.png');
  background-size: 0.6rem 0.32rem;
}
.header .heder-top .content .title .name {
  font-size: 0.32rem;
  font-weight: bold;
  line-height: 0.4rem;
}
.header .heder-top .content p {
  font-size: 0.24rem;
  font-weight: 200;
  line-height: 24px;
}
.header .heder-top .content .support .icon {
  width: 0.24rem;
  height: 0.24rem;
  display: inline-block;
  vertical-align: middle;
  background-image: url('../../imgs/decrease.png');
  background-size: 0.24rem 0.24rem;
  background-repeat: no-repeat;
}
.header .heder-top .content .support span {
  display: inline-block;
  font-size: 0.2rem;
  font-weight: 200;
  line-height: 24px;
}
.header .heder-top .support-count {
  position: absolute;
  right: 0.4rem;
  top: 1.3rem;
  color: white;
  width: 0.9rem;
  height: 0.5rem;
  line-height: 0.5rem;
  text-align: center;
  background: rgba(0, 0, 0, 0.2);
  font-size: 0.2rem;
  font-weight: 200;
  border-radius: 0.24rem;
}
.header .heder-top .support-count i {
  font-size: 0.2rem;
}
.header .header-bottom {
  background: rgba(7, 17, 27, 0.2);
  height: 0.6rem;
  padding: 0 0.44rem 0 0.44rem;
  position: relative;
  overflow: hidden;
}
.header .header-bottom .header-bottom-img {
  display: block;
  width: 0.45rem;
  height: 0.25rem;
  float: left;
  margin-top: 0.16rem;
  margin-right: 0.2rem;
  background: url('../../imgs/bulletin.png') no-repeat center center / 0.45rem 0.25rem;
}
.header .header-bottom .header-bottom-text {
  display: block;
  height: 0.6rem;
  line-height: 0.6rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 0.2rem;
  color: white;
  font-weight: 200;
}
.header .header-bottom .header-bottom-icon {
  position: absolute;
  right: 0.06rem;
  top: 0.16rem;
  color: white;
  font-size: 0.3rem;
}



