
.ratingselect .rating-type {
  padding: 0.36rem;
  margin: 0 0.36rem;
  border-bottom: 1px solid rgba(7, 17, 27, 0.2);
  font-size: 0;
}
.ratingselect .rating-type .block {
  display: inline-block;
  padding: 0.16rem 0.24rem;
  margin-right: 0.16rem;
  border-radius: 0.02rem;
  color: #4d555d;
  font-size: 0.24rem;
}
.ratingselect .rating-type .block.active {
  color: #fff;
}
.ratingselect .rating-type .block .count {
  margin-left: 0.04rem;
  line-height: 0.32rem;
  font-size: 0.16rem;
}
.ratingselect .rating-type .block.positive {
  background: rgba(0, 160, 220, 0.2);
}
.ratingselect .rating-type .block.positive.active {
  background: #00a0dc;
}
.ratingselect .rating-type .block.negative {
  background: rgba(77, 85, 93, 0.2);
}
.ratingselect .rating-type .block.negative.active {
  background: #4d555d;
}
.ratingselect .switch {
  padding: 0.24rem 0.36rem;
  line-height: 0.48rem;
  border-bottom: 1px solid rgba(7, 17, 27, 0.2);
  color: #93999f;
}
.ratingselect .switch.on .cc i {
  color: #00c580;
}
.ratingselect .switch .cc i {
  font-size: 0.48rem;
  color: #93999f;
  line-height: 0.48rem;
}
.ratingselect .switch .title {
  font-size: 0.28rem;
  color: #93999f;
  line-height: 0.48rem;
  margin-left: 0.08rem;
}



