
.moveleft-enter-active,
.moveleft-leave-active {
  transition: all 0.5s;
}
.moveleft-enter,
.moveleft-leave-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.detail {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0.96rem;
  z-index: 5;
  width: 100%;
  height: auto;
  background: #fff;
}
.detail .detail-content .image-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
}
.detail .detail-content .image-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.detail .detail-content .image-header .back {
  position: absolute;
  top: 0.15rem;
  left: 0.15rem;
}
.detail .detail-content .image-header .back i {
  padding: 0.15rem;
  font-size: 0.2rem;
  display: block;
  color: white;
}
.detail .detail-content .content {
  position: relative;
  padding: 0.36rem;
}
.detail .detail-content .content .title {
  line-height: 0.28rem;
  margin-bottom: 0.16rem;
  color: #07111b;
  font-size: 0.28rem;
  font-weight: 700;
}
.detail .detail-content .content .details {
  padding-bottom: 0.36rem;
  color: #93999f;
  line-height: 0.2rem;
  font-size: 0.2rem;
}
.detail .detail-content .content .details .rating {
  margin-left: 0.24rem;
}
.detail .detail-content .content .price {
  line-height: 0.48rem;
}
.detail .detail-content .content .price .now {
  font-size: 0.28rem;
  font-weight: normal;
  color: #f00214;
}
.detail .detail-content .content .price .old {
  font-size: 0.2rem;
  font-weight: 700;
  color: #93999f;
  text-decoration: line-through;
  margin-left: 0.2rem;
}
.detail .detail-content .content .cartcontrol-warp {
  position: absolute;
  right: 0.24rem;
  bottom: 0.24rem;
}
.detail .detail-content .content .buy {
  position: absolute;
  right: 0.36rem;
  bottom: 0.36rem;
  width: 1.48rem;
  height: 0.48rem;
  z-index: 10;
  line-height: 0.48rem;
  padding: 0 0.24rem;
  box-sizing: border-box;
  font-size: 0.2rem;
  border-radius: 0.24rem;
  color: #fff;
  background: #00a0dc;
}
.detail .info {
  padding: 0.36rem;
}
.detail .info .title {
  line-height: 0.28rem;
  margin-bottom: 0.12rem;
  font-size: 0.28rem;
  color: #07111b;
}
.detail .info .text {
  line-height: 0.48rem;
  padding: 0 0.16rem;
  font-size: 0.24rem;
  font-weight: 200;
  color: #4d555d;
}
.detail .rating {
  padding-top: 0.36rem;
}
.detail .rating .title {
  line-height: 0.28rem;
  margin-left: 0.36rem;
  font-size: 0.28rem;
  color: #07111b;
}
.detail .rating .rating-warp {
  padding: 0 0.36rem;
}
.detail .rating .rating-warp .rating-item {
  position: relative;
  padding: 0.32rem 0;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.detail .rating .rating-warp .rating-item .user {
  position: absolute;
  right: 0;
  top: 0.32rem;
  font-size: 0;
  line-height: 0.24rem;
}
.detail .rating .rating-warp .rating-item .user .name {
  display: inline-block;
  vertical-align: top;
  font-size: 0.3rem;
  margin-right: 0.12rem;
  color: #93999f;
}
.detail .rating .rating-warp .rating-item .user .avatar {
  border-radius: 50%;
}
.detail .rating .rating-warp .rating-item .time {
  line-height: 0.24rem;
  font-size: 0.2rem;
  color: #93999f;
  margin-bottom: 0.12rem;
}
.detail .rating .rating-warp .rating-item .text {
  line-height: 0.32rem;
  font-size: 0.24rem;
  color: #07111b;
}
.detail .rating .rating-warp .rating-item .text span i {
  margin-right: 0.08rem;
  line-height: 0.32rem;
  font-size: 0.24rem;
}
.detail .rating .rating-warp .rating-item .text span i.cc {
  color: #00a0dc;
}
.detail .rating .rating-warp .rating-item .text span i.dd {
  color: #93999f;
}
.detail .rating .rating-warp .no-rating {
  padding: 0.32rem 0;
  font-size: 0.24rem;
  color: #93999f;
}



